/*
*
* =====================
* GENERAL
* =====================
*
*/

body {
    overflow-x: hidden;
}

.text-small {
    font-size: 0.9rem !important;
}

.text-xl {
    @include media-breakpoint-up(lg) {
        font-size: 4.5rem !important;
    }
}

.text-lg {
    @include media-breakpoint-up(lg) {
        font-size: 3.6rem !important;
    }
}

.progress {
    height: 1.5rem !important;
    border-radius: 0 !important;
}

.lined {
    &::after {
        content: '';
        display: block;
        width: 6.25rem;
        height: 2px;
        margin: 1.25rem 0;
        background: $brand-primary;
    }
}

.text-muted {
    color: #999 !important;
}

strong {
    font-weight: $font-weight-bold;
}

@each $color,
$value in $theme-colors {
    .progress-#{$color}-1 {
        background: lighten($value, 20%);
    }
    .progress-#{$color}-2 {
        background: lighten($value, 15%);
    }
    .progress-#{$color}-3 {
        background: lighten($value, 10%);
    }
    .progress-#{$color}-4 {
        background: lighten($value, 5%);
    }
    .progress-#{$color}-5 {
        background: lighten($value, 0);
    }
}


.customer {
    filter: grayscale(100%);
    transition: all 0.3s;

    &:hover {
        filter: none;
    }
}


.social-link {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $brand-primary;

    &:hover, &:focus {
        text-decoration: none;
        color: #fff;
    }

    &.facebook {
        background: #4460ae;
    }
    &.twitter {
        background: #3cf;
    }
    &.google-plus {
        background: #c21f25;
    }
    &.instagram {
        background: #cd4378;
    }
    &.email {
        background: $success;
    }
}

.text-uppercase {
    letter-spacing: 0.1em;
}


#map {
    height: 500px;
}
